import { observer } from "mobx-react";
import { useStore } from "../store";
import { Col, Input } from "antd";
import InfiniteScroll from "react-infinite-scroll-component";
import { List, Skeleton, Divider } from "antd";
import { useEffect } from "react";

const Component = observer(() => {
  const { HistoryStore } = useStore();
  useEffect(() => {
    HistoryStore.getListLength();
    HistoryStore.getHistory();
    return () => {
      HistoryStore.reset();
    };
  }, []);
  const loadMore = () => {
    HistoryStore.getHistory();
  };
  return (
    <InfiniteScroll
      dataLength={HistoryStore.list.length}
      next={loadMore}
      hasMore={HistoryStore.hasMore}
      loader={<Skeleton avatar paragraph={{ rows: 1 }} active />}
      endMessage={<Divider plain>It is all, nothing more ğŸ¤</Divider>}
      scrollableTarget="scrollableDiv"
    >
      <List
        style={{ width: "100%" }}
        dataSource={HistoryStore.list}
        renderItem={(item: any) => (
          <List.Item key={item.id}>
            <Col span={8}>
              <img
                src={item.attributes.link}
                alt=""
                style={{ width: "200px" }}
              />
            </Col>
            <Col span={16}>
              <ul>
                <Col span={24}>
                  <li>
                    <strong>åˆ†äº«é“¾æ¥ :</strong>
                    <Col span={8}>
                      <Input
                        type="text"
                        readOnly
                        value={item.attributes.link}
                      />
                    </Col>
                  </li>
                </Col>
                <br />
                <li>
                  <strong>HTML :</strong>
                  <Col span={8}>
                    <Input
                      type="text"
                      readOnly
                      value={
                        "<blockquote className=" +
                        "imgur-embed-pub" +
                        " lang" +
                        "=" +
                        "en" +
                        " data-id" +
                        "=" +
                        item.attributes.imageId +
                        ">" +
                        "<a" +
                        " href=" +
                        item.attributes.link +
                        ">View post on imgur.com</a>" +
                        " </blockquote><script async src=" +
                        "//s.imgur.com/min/embed.js" +
                        " charSet=" +
                        "utf-8" +
                        "></script>"
                      }
                    />
                  </Col>
                </li>

                <br />

                <li>
                  <strong>Markdown :</strong>
                  <Col span={8}>
                    <Input
                      type="text"
                      readOnly
                      value={"[Imgur](" + item.attributes.link + ")"}
                    />
                  </Col>
                </li>
              </ul>
            </Col>
          </List.Item>
        )}
      />
    </InfiniteScroll>
  );
});
export default Component;
